.trusted-brands-cont {
    max-width: 1224px;
    min-height: 253px;
    margin: 200px auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(44, 53, 61, 0.2);
    
    border-radius: 25px;
    
}

.trusted-brands-inner {
    max-width: 1059px;
    width: 100%;
    min-height: 142px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.trusted-brands {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.trusted-brands_heading {
    font-weight: 500;
    font-size: em(24);
    line-height: 1.5;
    /* identical to box height */


    color: rgba(255, 255, 255, 0.8);

}

.microsoft-name {
    width: 193px;
    height: 42px;
    transition: 0.56s cubic-bezier(0.19, 1, 0.22, 1);
    path {
        transition: 0.2s ease-in-out;
    }
}

.google-name {
    width: 157px;
    height: 51px;
    transition: 0.56s cubic-bezier(0.19, 1, 0.22, 1);
    path {
        transition: 0.2s ease-in-out;
    }
}

.slack-name {
    width: 194px;
    height: 49px;
    transition: 0.56s cubic-bezier(0.19, 1, 0.22, 1);
    path {
        transition: 0.2s ease-in-out;
    }
}

.wordpress-name {
    width: 194px;
    height: 44px;
    transition: 0.56s cubic-bezier(0.19, 1, 0.22, 1);
    path {
        transition: 0.2s ease-in-out;
    }
}

