.new-tools-section {
    max-width: 1224px;
    margin: 200px auto 0;
    display: flex;
    padding-top: 20px;
    justify-content: space-between;
    overflow: hidden;
    flex-wrap: wrap;
}

.nts-newcommer-apps {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    min-height: 638px;
    max-width: 624px;
    margin-left: -8px;
    margin-right: -8px;
}

.mp-app.modif {
    max-width: 288px;
    min-height: 284px;
    margin-left: 12px;
    margin-right: 12px;
    margin-bottom: 50px;
    padding: 28px 21px 28px 22px;
    
}

.mp-app-icon.zeplin {
    margin-bottom: 31px;
}

.mp-app-icon.php {
    margin-bottom: 31px;
}

.mp-app-icon.toolbox {
    margin-bottom: 31px;
}

.mp-app-icon.procreate {
    margin-bottom: 31px;
}

.nts-newcommer-div {
    max-width: 427px;
    min-height: 300px;
}

.nts-nd-header {
    font-weight: 500;
    font-size: em(48);
    line-height: 130%;
    /* or 62px */


    color: rgba(255, 255, 255, 0.9);
}

.nts-nd-text {
    font-style: normal;
    font-weight: 500;
    font-size: em(18);
    line-height: 167%;
    margin-top: 30px;

    color: rgba(255, 255, 255, 0.55);
}

.nts-nd-button {
    width: 198px;
    height: 58px;   
    background: #FF6E30;
    border-radius: 8px;
    color: white;
    font-style: normal;
    font-weight: 500;
    font-size: em(18);
    line-height: 130%;
    /* identical to box height */
    margin-top: 60px;


}

.nts-nd-button.media {
    display: none;
    margin-bottom: 29px;
}

