@import '_header.scss';


.firstpage {
    max-width: 1440px;
    width: 100%;
    min-height: 700px;
    margin: 0 auto;
    position: relative;
    padding-top: 26px;
    padding-bottom: 70px;
}

.fp-drawing {
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: 1;
    
}

//anim

.header, .fp-drawing, .firstpage-flex, .fp-icons-div, .mp_heading,.mp_description, .mp-apps-cont, .trusted-brands-cont, .nts-nd-header, .nts-nd-button, .nts-nd-text, .nts-newcommer-apps,.load-more-button, .swiper-section, .b-c-d_heading, .b-c-d_text, .fp-flex-inputform.modif, .footer-main, .footer-info_contact , .footer-info_categories, .footer-info_companyinfo {
    opacity: 0;
}

.fp-drawing.anim {
    animation-name: drawing-up;
    animation-duration: 0.7s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
}

.firstpage-flex.anim {
    animation-name: fp-flex-down;
    animation-duration: 0.7s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
}


.fp-icons-div.anim {
    animation-name: drawing-up;
    animation-duration: 0.7s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    animation-delay: 0.4s;
}

