@keyframes apps-grid {
    0% {
        transform: translateY(5%);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes fp-flex-down {
    0% {
        transform: translateY(-20%);
        opacity: 0;
    }

    100% {
        transform: translateY(0%);
        opacity: 1;
    }
}

@keyframes drawing-up {
    0% {
        transform: translateY(20%);
        opacity: 0;
        
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.fp-icons-div {
    position: absolute;
    bottom: 0;
    left: 120px;
}

.fp-icon path {
    transition: 0.2s ease-in-out;
}

.fp-icon+.fp-icon {
    margin-left: 28px;
}

.firstpage-flex {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    width: 100%;
    min-height: 354px;
    margin-top: 104px;
    margin-left: 111px;
    margin-right: 111px;
    z-index: 2;
    position: relative;
}



.fp-flex-heading {
    font-style: normal;
    font-weight: 500;
    font-size: em(50);
    line-height: 140%;
    color: rgba(255, 255, 255, 0.9);
}

.fp-flex-text {
    font-style: normal;
    font-weight: normal;
    font-size: em(18);
    line-height: 167%;
    /* or 167% */
    max-width: 487px;
    margin-top: 30px;
    color: rgba(255, 255, 255, 0.55);
    margin-bottom: 60px;
}


.fp-flex-inputform {
    height: 64px;
    display: flex;
    align-items: center;
    max-width: 497.66px;
    width: 100%;
    border-radius: 15px;
    background: #283036;
    border: 1.5px solid rgba(255, 255, 255,0.05);
    padding: 8px 11.04px 8px 18.06px;
    
}

.fp-flex-inputform-search {
    width: 24.08px;
    
}

.fp-flex-input {
    width: 190px;
    height: 21px;
    background: transparent;
    outline: none;
    border: none;
    color: rgba(white, 0.8);
    caret-color: #837A93;
    margin-left: 13px;

}

.fp-flex-input::placeholder {
    font-size: em(14);
    line-height: 130%;
    /* identical to box height */


    color: rgba(255, 255, 255, 0.38);
}

.fp-flex-inputform-button {
    background: #FF6E30;
    border-radius: 8px;
    width: 130.43px;
    height: 48px;
    font-weight: 600;
    font-size: em(16);
    line-height: 140%;
    /* identical to box height */

    margin-left: auto;
    color: #FFFFFF;
}

