.menu {
    min-height: 100px;
    display: none;
    width: 100%;
    background: #252e36;
    overflow: hidden;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    transition: 0.7s ease-in-out;
    backface-visibility: hidden;
    perspective: 1000px;
    border-bottom: 20px solid #FF6E30;
    position: absolute;
    top: 0;
    left: 0;
    top: -27px;
    right: 0;
    z-index: 4;
    padding-bottom: 15px;
    align-items: flex-end;
}



.header-navlink.modif {
    margin: 18px;
}

.header-sign-cont.modif {
    display: none;
}



.menu .header-navbar {
    display: flex;
    justify-content: space-evenly;
    max-width: initial;
}