.swiper-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1160px;
    margin: 150px auto 0;
}

.swiper {
    max-width: 849px;
    width: 100%;
    min-height: 492px;
}

.swiper-pagination-bullet {
    width: 44px;
    height: 6px;
    background: #283036;
    border-radius: 4px;
    margin: 0 0 0 18px !important;
}


.swiper-pagination-bullet-active {
    background: #FF6E30;

}

.swiper-pag {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.next-but {
   
    width: 48px;
    height: 48px;
}

.prev-but {
    width: 48px;
    height: 48px;
    transform: scale(-1,1);
}

.prod-manage-slide {
    display: flex;
    overflow: hidden;
}

.p-m-s_image {
    width: 392px;
    height: 492px;
   
}

.p-m-s_block-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: em(14);
  line-height: 214%;
  /* or 214% */
  
  
  letter-spacing: 0.015em;
  
  color: rgba(255, 255, 255, 0.78);
  
  
}

.p-m-s_block {
    position: relative;
    margin-top: 128px;
    margin-left: 6.71%;

}

.p-m-s_block-symb {
    font-family: Lora;
    color: transparent;    
      -webkit-background-clip: text;
      background-clip: text;
      background-image: linear-gradient(to top, rgba(255, 255, 255, 0.5), rgba(252, 252, 252, 0));
      user-select: none;
      font-size: em(250);
        line-height: 1.28;
        /* identical to box height */
        position: absolute;
        top: -100px;
        left: -85px;
        
       
  
}

// animations

.p-m-s_block-symb {
    
    transform: translateX(100px) rotate(19.17deg);
}

.swiper-slide-active {
     .p-m-s_block-symb {
        transform: translateX(0px) rotate(19.17deg);
        transition: 0.7s ease-in-out;
    }

    .p-m-s_image, .p-m-s_block-text {
        transform: translateX(0px);
        transition: 0.7s ease-in-out;
    }
}

.p-m-s_block-text {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    
    max-height: 3000em;
}

.p-m-s_image {
    
    transform: translateX(-100px);
}

.p-m-s_block-text {
    text-align: center;
    transform: translateX(100px);
}
