.footer {
    min-height: 351px;
    width: 100%;
    margin: 0 auto;
    border-top: 0.5px solid rgba(255, 255, 255, 0.15);
    margin-top: 240px;
}

.footer-inside {
    max-width: 1224px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 70px auto 0;
}

.footer-main {
    max-width: 260px;
    min-height: 183px;
}

.footer-info {
    max-width: 808px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.footer-info li {
    transition: 0.2s ease-out;
}

.footer-main_copyright {
    font-weight: 500;
    font-size: em(16);
    line-height: 1.5;
    /* identical to box height */

    margin-top: 25px;
    color: rgba(255, 255, 255, 0.9);
}

.footer-main_text {
    font-style: normal;
    font-weight: normal;
    font-size: em(14);
    line-height: 179%;
    /* or 179% */
    margin-top: 21px;

    color: rgba(255, 255, 255, 0.55);
}

.f-i_headingli {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: em(20);
    line-height: 1.5;
    /* identical to box height */


    color: rgba(255, 255, 255, 0.9);
}

.f-i_headingli ~ li {
    margin-top: 10px;
}

.f-i_headingli+li {
    margin-top: 20px;
}

.footer-info_companyinfo {
    max-width: 269px;
    flex-grow: 1;

}