.header {
    max-width: 1224px;
    width: 100%;
    height: 48px;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
   
    position: relative;
    z-index: 3;

}

.header-logo {
    width: 38px;
    height: 38px;
}

.header-nav-btn {
    display: none;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    background: #FF6E30;
   
}

.empty-head-nav {
    display: none;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    visibility: hidden;
}

.header-nav-btn.active {
    position: absolute;
   
    animation-name: btnactive;
    animation-duration: 0.6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    animation-delay: 0.1s;
}

.header-nav-btn.modify {
    position: absolute;
    z-index: 3;
    top: 5px;
    right: 15px;
}

.header-nav-btn::before {
    content: '';
    display: block;
    width: 100%;
    height: 4px;
    background: white;
    transform: translateY(-5px);
    border-radius: 6px;
    position: relative;
    top: 0;
}

.header-nav-btn::after {
    position: relative;
    content: '';
    display: block;
    width: 100%;
    height: 4px;
    background: white;
    transform: translateY(5px);
    border-radius: 6px;
    top: 0;
}

.header-nav-btn.active::after {
   top: -2px;
    transform: rotate(-135deg) translateY(0px);
    transition: 0.3s ease-in-out;
    transition-delay: 0.4s;
}

.header-nav-btn.active::before {
  top:  2px;
    transform: rotate(135deg) translateY(0px);
    transition: 0.3s ease-in-out;
    transition-delay: 0.4s;
    
}


.header-nav-btn.inactive::after {
    top: -2px;
    transform: rotate(-45deg) translateY(0px);
  
   
}

.header-nav-btn.inactive::before {
    top:  2px;
    transform: rotate(45deg) translateY(0px);
  
    

}




.header-nav-btn.inactive {
    position: absolute;
    
    z-index: 3;
    animation-name: btninactive;
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    
    top: 120px;
        right: 15px;
}



@keyframes btninactive {
    0% {
        transform: scale(1);

    }

    25% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(0);
    }
}

@keyframes btnactive {
    0% {
        top: 5px;
        right: 15px;

    }

    100% {
        top: 120px;
        right: 15px;
        
    }
}



.header-logo-text {
    display: flex;
    font-size: em(22);
    line-height: 120%;
    align-items: center;
    max-height: 100%;
    
    min-width: fit-content;
}

.header-text {
    margin-left: 12px;
}

.header-navbar {
    max-width: 431px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.header-navlink {
    font-size: 1em;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.55);
    transition: 0.2s ease-out;
}


.header-sign-cont {
    display: flex;
    align-items: center;
    height: 100%;
    
    
}

.heaer-login {
    color: rgba(255, 255, 255, 0.55);
    font-weight: 600;
    font-size: 1em;
}

.header-signup {
    background: #FF6E30;
    width: 118px;
    height: 100%;
    border-radius: 8px;
    
    font-size: em(16);
    line-height: 120%;
    color: #FDF5FF;
    font-weight: 600;
    margin-left: 41px;
    padding: 0;
}