@media all and (max-width: 1285.87px) {
    #root {
        padding: 0 8px;
    }

    .trusted-brands-inner {
        padding-left: 13px;
        padding-right: 13px;
    }

    .footer-info_companyinfo {
        max-width: fit-content;
    }

    .footer-info {
        max-width: 683px;
    }
    
}



@media all and (max-width: 1072px) {

    .nts-nd-button {
        display: none;
    }
    .nts-nd-button.media {
        display: inline-block;
        margin-top: 45px;
    }

    .nts-newcommer-apps  {
        margin-left: 0;
        margin-right: 0;
    }

    .mp-app.modif {
        margin-left: 0;
        margin-right: 0;
    }

    .new-tools-section {
        justify-content: center;
    }

    .swiper-section {
        margin-top: 121px;
    }

    .nts-newcommer-div {
        text-align: center;
    }

    .nts-newcommer-apps {
        max-width: 950px;
    }

    .mp-app.modif {
        max-width: initial;
    }


    
}

@media  all and (max-width: 992px) {
    .header-navbar {
        display: none;
    }

    .menu {
        display: flex;
    }

   .header {
       padding: 0 15px;
   }

    .header-signup {
        height: 38px;
    }

    .header-nav-btn {
        display: block;
    }

    .empty-head-nav {
        display: block;
    }

    .header-nav-btn {
        display: block;
    }

    .footer-inside {
        flex-direction: column;
        max-width: 683px;
        margin: 0 auto;
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .footer-info {
        flex-wrap: wrap;
        margin-left: -20px;
        margin-right: -20px;
    }

    .footer-info_contact, .footer-info_categories, .footer-info_companyinfo {
        margin: 20px;
        
    }


    .footer-main {
    
        margin-bottom: 40px;
    }


    .fp-drawing.anim {
        animation-name: drawing-uplow;
    }

    @keyframes drawing-uplow {
        0% {
            transform: translateY(20%);
            opacity: 0;
            
        }
    
        100% {
            transform: translateY(0);
            opacity: 0.3;
        }
    }

    .microsoft-div-min, .wordpress-div-min, .google-div-min, .slack-div-min {
        display: block;
    }

    .microsoft-div, .wordpress-div, .google-div, .slack-div {
        display: none;
    }

    .trusted-brands-cont {
        max-width: 500px;
        padding-left: 50px;
        padding-right: 50px;
        
    }

    .trusted-brands_heading {
        text-align: center;
    }

}

@media all and (max-width: 833px) {
    .firstpage-flex {
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        perspective: 1000px;
    }

    .fp-icons-div {
        left: 48%;
        left: calc(50% - 81px);
        bottom: -70px;
    }
}

@media all and (max-width: 760px) {
    .prod-manage-slide {
        flex-direction: column;
        align-items: center;
    }

    
    

   

    .p-m-s_block {
        margin: 30px auto 0;
    }

    .p-m-s_block-symb {
        left: initial;
        top: -400px;
        right: 10%;
       
    }

    
    .p-m-s_block-text {
        text-align: center;
    }
}

@media all and (max-width: 600px) {
    .footer-inside {
        align-items: flex-start;
        margin-left: auto;
        margin-right: auto;
       
        max-width: 385.6px;
    }

    .footer-info {
        justify-content: flex-start;
        
    }
}

@media all and (max-width: 576px) {

    @keyframes btnactive {
        0% {
            top: 5px;
            right: 15px;
    
        }
    
        100% {
            top: 240px;
            right: 15px;
            
        }
    }

    .header-sign-cont {
        display: none;
        height: initial;
    }

    .header-nav-btn.active {
        animation-name: btnactive;
        animation-duration: 0.5s;
    }

    .p-m-s_block-symb {
        right: 5%;
    }

    .menu {
        
        left: -8px;
        right: -8px;
        width: initial;
        align-items: center;
        border-radius: 0;
        
    }

    


.header-nav-btn.inactive {
    top: 240px;
}
    


    .header-sign-cont.modif {
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        margin-right: 18px;
    }
    
    .header-signup.modif {
        margin-left: 0;
        margin-top: 30px;
    }

    .header-navbar.modif {
        flex-direction: column;
    }

    
    
    .header-login.modif {
        font-size: 1.3em;
        padding-right: 8px;
    }

    

    .header-navbar.modif {
        flex-wrap: wrap;
    }

  
    
}

@media all and (max-width: 494px) {
    .next-but, .prev-but {
        display: none;
    }

    .swiper-pag {
        margin-top: 30px;
    }
}

@media all and (max-width: 421px) {
    
    .mp-app {
        flex-basis: 290px;
    }

    

    .vsmin {
        display: block;

    }

    .vs {
        display: none;
    }

    .footer-inside {
        width: min-content;
    }

    .p-m-s_image {
        max-width: 290px;
        height: auto;
    }

    .p-m-s_block-text {
        font-size: 0.75em;
    }

    

  
    
}




// pointer events

@media (hover: hover) and (pointer: fine) {
    .mp-app:hover {
        transition-property: transform;
        transform: translateY(-20px);
        background: rgb(255,128,75);
        background: -moz-linear-gradient(207deg, rgba(255,128,75,1) 0%, rgba(61,47,44,1) 23%, rgba(30,37,43,1) 48%);
        background: -webkit-linear-gradient(207deg, rgba(255,128,75,1) 0%, rgba(61,47,44,1) 23%, rgba(30,37,43,1) 48%);
        background: linear-gradient(207deg, rgba(255,128,75,1) 0%, rgba(61,47,44,1) 23%, rgba(30,37,43,1) 48%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff804b",endColorstr="#1e252b",GradientType=1);
        
        outline: 2px solid rgba(255, 255, 255, 0.1) ;

        .mp-app_icon-visit {
            background: rgba(255, 110, 48, 0.7);
            
            color: #FFFF;
        }

        .mp-app_icon-visit:hover {
            background: rgba(255, 110, 48, 1);
        }

        
    }

    .mp-app_icon-add:hover path {
        fill: rgba(255, 165, 55, 0.55);
        
    }

    .header-navlink:hover {
        color: rgba(255, 165, 55, 0.55);
    }

    .nts-nd-button:hover, .header-signup:hover, .fp-flex-inputform-button:hover {
        background: rgba(255, 110, 48, 0.7);
    }

    .header-logo path {
        transition: 0.2s ease-in-out;
    }

    .header-logo:hover {
        .logo-path2 {
            transform: translate3d(-2px, 2px, 0);
        }

        .logo-path1 {
            transform: translate3d(2px, -2px, 0);
        }
    }

   
    .mp-app_icon-like:hover path {
        fill: #fd3b41;
        
    }

    .header-nav-btn, .header-signup, .header-login, .header-logo, .header-text, .fp-flex-inputform-button, .fp-icon, .mp-app_icon-visit, .mp-app_icon-like, .mp-app_icon-add, .load-more-button, .next-but, .prev-but, .footer-info li, .google-name, .slack-name, .wordpress-name, .microsoft-name, .nts-nd-button , .header-navlink {
        cursor: pointer;
    }

   

    .footer-info li:hover {
        color: rgba(255, 165, 55, 0.55);
    }

    .footer-info .f-i_headingli {
        
        cursor: auto;
    }

    .footer-info .f-i_headingli:hover {
        color: inherit;
    }

    .microsoft-name:hover path, .google-name:hover path, .slack-name:hover path, .wordpress-name:hover path, .fp-icon:hover path {
        fill: rgba(255, 165, 55, 1);
        
    }

    .microsoft-name:hover, .google-name:hover, .slack-name:hover, .wordpress-name:hover, .fp-icon:hover {
        transform: scale(1.1);
        
    }

    .load-more-button:hover::before {
     
        transform: translateY(0%);
    
}

    .load-more-button:hover {
        color: #1E252B;
    }
}



