}

.b-c-d_heading {
    font-weight: 500;
    font-size: em(48);
    line-height: 130%;
    /* or 62px */

    text-align: center;

    color: rgba(255, 255, 255, 0.9);

}

.b-c-d_text {
    max-width: 427px;
    font-style: normal;
    font-weight: 500;
    font-size: em(18);
    line-height: 167%;
    /* or 167% */
